import './App.css';
import NavBar from './Components/NavBar/NavBar.js'
import Home from './Home/Home.js';
import SignIn from './Components/SignIn/SignIn.js'
import SignUp from './Components/SignUp/SignUp.js'
import Products from './Components/Products/Products.js'
import Cart from './Components/Cart/Cart'
import About from './Components/About/About.js'
import { useState, useEffect } from 'react';
import {auth} from './firebase'
import {HashRouter as Router, Route, Switch} from 'react-router-dom'


function App() {
  const [user, setUser] = useState(null)
  useEffect(() => {
    const unsuscribe = auth.onAuthStateChanged(
      userAuth=>{
        if(userAuth){
          console.log(userAuth)
          const user = {
            uid: userAuth.uid,
            email: userAuth.email
          }
          setUser(user)
        }
        else{
          setUser(null)
        }
      }
    )
    return unsuscribe
  }, [])
  
  return (
    <Router>
    <div className="App">
      <NavBar status={user? true: false}/>
      <div id="content">
        <Switch>
          <Route path='/' exact component={Home}/>
          <Route path='/About' exact component={About}/>
          <Route path='/product' >
          {user? <Products/>:<> <Heading tag = "SignIn to access the Prodcuts"/> <SignIn/></>  }
          </Route>
          <Route path='/cart' >
          {user? <Cart/>:<> <Heading tag = "SignIn to access your Cart"/> <SignIn/></>  }
          </Route>
          {/* <Route path='/product' component={SignIn}/> */}
          <Route path='/signUp' component={SignUp}/>
        </Switch>
        </div>
    </div>
    </Router>
  );
}

const Heading= (props) => {
  return (
    <div className="heading-wrapper">
      <h2>
        {props.tag}
      </h2>
    </div>
  )
}


export default App;
