import React, { Component } from 'react';
import './NavBar.css';
import {MenuItems} from './MenuItems'
import {auth} from '../../firebase'
import { Link } from 'react-router-dom';
class NavBar extends Component {
  state = {
    clicked: false
  }

  clickHandle=()=>{
    this.setState({
      clicked: !this.state.clicked
    })
  }

  render() {
    return (
      <nav>
      <div id="hamburger" onClick={this.clickHandle}>
          <i id="hamburger-icon" className={this.state.clicked?'fas fa-times' : 'fas fa-bars'}> </i>
      </div>
      <h2 className="logo">
          <span className='kali-blue'>$</span>easy_Agro</h2>

      <ul className = {this.state.clicked ? 'menu-options menu-visible':'menu-options menu-hidden'}>
          {MenuItems.map((item,index)=>{
            return(
              <li key={index} onClick={this.clickHandle}>
                <Link to={item.url} className={item.cName}>{item.title}</Link>
              </li>
            )
          })}
                {this.props.status?
                  <li onClick={()=>auth.signOut()}>
                    <Link to="/" className="nav-links"> Sign Out</Link>
                  </li>
                :
                <li onClick={this.clickHandle} >
                <Link to="/product" className="active nav-links"> Sign In </Link></li>
                }
            </ul>
        </nav>
    )
  }
}

export default NavBar;


